import React, { PropTypes } from 'react';
import { browserHistory } from 'react-router';
import ClothAlterations from './ClothAlterations';
import Alterations from './Alterations';

import { Wrapper, Block, Header2, Title2, Logo, Button3, Center, Total, ClothesList, ErrorText, Cloth, Add, ClothText }
  from './styledComponents';
import ProgressBar from './order/ProgressBar';
import LogoImg from '../assets/logo1.png';

const Step2 = ({ orderItems, toggleAlteration, total, showChoicesCloth, openedCategory, openCategory,
setChoicesCloth, close, showChoicesItemIndex, isButtonValid, goNext, hasError }) => (
  <Wrapper>
    <Block id="alterations">
      <Header2>
        <Logo src={LogoImg} />
        <ProgressBar step={2} />
        <Title2>
          Je choisis les retouches pour chacun de mes vêtements
        </Title2>
      </Header2>

      <ClothesList>
        {orderItems.map((item, index) => {
          const itemNumber = orderItems
            .filter((item2, index2) => item2.slug === item.slug && index >= index2)
            .length;
          return (<ClothAlterations
            itemNumber={itemNumber}
            orderItem={item}
            toggleChoices={cloth => setChoicesCloth(cloth, index)}
            key={`${item.slug}-${itemNumber}`}
          />);
        })}
        {showChoicesCloth ?
          <Alterations
            cloth={showChoicesCloth}
            openedCategory={openedCategory}
            openCategory={openCategory}
            close={close}
            orderItem={orderItems[showChoicesItemIndex]}
            toggleAlteration={alterationId => toggleAlteration(showChoicesItemIndex, alterationId)}
          />
          : null
        }
        <Cloth onClick={() => browserHistory.push('/order/step1')}>
          <ClothText>Ajouter un</ClothText>
          <ClothText>vêtement</ClothText>
          <Add>+</Add>
        </Cloth>
      </ClothesList>

      <Total>Total : {total.toFixed(2)}€</Total>

      {hasError && <ErrorText>Merci de sélectionner des retouches pour tous les vêtements</ErrorText>}

      <Center>
        <Button3 isNotValid={!isButtonValid} onClick={goNext}>{'> Valider <'}</Button3>
      </Center>
    </Block>
  </Wrapper>
);

Step2.propTypes = {
  orderItems: PropTypes.arrayOf(PropTypes.shape()).isRequired,
  toggleAlteration: PropTypes.func.isRequired,
  total: PropTypes.number.isRequired,
  showChoicesCloth: PropTypes.shape({}),
  showChoicesItemIndex: PropTypes.number,
  openCategory: PropTypes.func.isRequired,
  openedCategory: PropTypes.string,
  setChoicesCloth: PropTypes.func.isRequired,
  close: PropTypes.func.isRequired,
  goNext: PropTypes.func.isRequired,
  isButtonValid: PropTypes.bool.isRequired,
  hasError: PropTypes.bool,
};

Step2.defaultProps = {
  showChoicesCloth: undefined,
  showChoicesItemIndex: undefined,
  openedCategory: undefined,
  hasError: false,
};

export default Step2;



