import React, { Component, PropTypes } from 'react';
import { connect } from 'react-redux';
import { browserHistory } from 'react-router';
import Step2 from '../../components/Step2';

class Step2Container extends Component {
  constructor() {
    super();
    this.state = {};
    this.openCategory = this.openCategory.bind(this);
    this.setChoicesCloth = this.setChoicesCloth.bind(this);
    this.goNext = this.goNext.bind(this);
  }

  setChoicesCloth(cloth, index) {
    this.setState({ showChoicesCloth: cloth, showChoicesItemIndex: index });
  }

  openCategory(openedCategory) {
    this.setState({ openedCategory });
  }

  toggleAlteration(itemIndex, alterationId) {
    this.props.dispatch({ type: 'ORDER.TOGGLE_ALTERATION', itemIndex, alterationId });
  }

  goNext() {
    if (this.isOrderValid()) {
      browserHistory.push('/order/step3');
    } else {
      this.setState({ hasError: true });
    }
  }

  isOrderValid() {
    return this.props.orderItems && this.props.orderItems.length > 0
      && !this.props.orderItems.find(item => !item.alterations || item.alterations.length === 0);
  }

  render() {
    const isButtonValid = this.isOrderValid();
    return (
      <Step2
        orderItems={this.props.orderItems}
        total={this.props.total}
        toggleAlteration={(itemIndex, alterationId) => this.toggleAlteration(itemIndex, alterationId)}
        showChoicesCloth={this.state.showChoicesCloth}
        showChoicesItemIndex={this.state.showChoicesItemIndex}
        openedCategory={this.state.openedCategory}
        openCategory={this.openCategory}
        close={() => this.setChoicesCloth()}
        setChoicesCloth={this.setChoicesCloth}
        isButtonValid={isButtonValid}
        goNext={this.goNext}
        hasError={this.state.hasError}
      />
    );
  }
}

Step2Container.propTypes = {
  orderItems: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
  dispatch: PropTypes.func.isRequired,
  total: PropTypes.number.isRequired,
  openedCategory: PropTypes.shape({}),
  showChoicesCloth: PropTypes.shape({}),
};

Step2Container.defaultProps = {
  openedCategory: undefined,
  showChoicesCloth: undefined,
};

const mapStateToProps = state => ({
  orderItems: state.order.orderItems,
  total: state.order.total,
  openedCategory: state.order.openedCategory,
});

const mapDispatchToProps = dispatch => ({ dispatch });

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Step2Container);



