import React, { PropTypes } from 'react';
import { Categories, Category, Header3, Title4, Center, Button, Choice, Price } from './styledComponents';

function renderItem(item, toggleAlteration, orderItem) {
  const isSelected = orderItem.alterations && orderItem.alterations[item.id];
  return (
    <Choice isSelected={isSelected} onClick={() => toggleAlteration(item.id)} key={item.id}>
      {item.name}
      <Price>{item.price}€</Price>
    </Choice>
  );
}

const Alterations = ({ cloth, openedCategory, openCategory, close, toggleAlteration, orderItem }) => (
  <Categories>
    {cloth.categories.map(category =>
      <div key={category.name}>
        <Category onClick={() => openCategory(category.name)}>
          <Header3>
            <Title4>
              {openedCategory === category.name ?
                <img src="https://icon.now.sh/chevron/up/12" alt="chevron icon" />
                : <img src="https://icon.now.sh/chevron/down/12" alt="chevron icon" />
              }
              {` ${category.name}`}
            </Title4>
          </Header3>
        </Category>
        {openedCategory === category.name &&
          category.items.map(item => renderItem(item, toggleAlteration, orderItem))}
      </div>,
    )}
    <Center>
      <Button onClick={close}>Valider ce vêtement</Button>
    </Center>
  </Categories>
);

Alterations.propTypes = {
  cloth: PropTypes.shape({}).isRequired,
  openCategory: PropTypes.func.isRequired,
  openedCategory: PropTypes.string,
  close: PropTypes.func.isRequired,
  toggleAlteration: PropTypes.func.isRequired,
  orderItem: PropTypes.shape({}).isRequired,
};

export default Alterations;



